import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, LoadingController, Loading, AlertController } from 'ionic-angular';

import { DynaPage } from '../dyna/dyna';
import { ErrorPage } from './../error/error';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';


/**
 * Generated class for the SpHomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sp-home',
  templateUrl: 'sp-home.html',
})
export class SpHomePage {

  selectedTab = "profile";
  spList = [{
    img: "assets/imgs/temple.jpg",
    name: "Temple Tour",
    duration: "2 hours",
    workingHours: "8:30 AM to 4:30 PM",
    rate: "200",
    currency: "Rs.",
    active: true
  },
  {
    img: "assets/imgs/church.jpg",
    name: "Chruch Tour",
    duration: "1.5 hours",
    workingHours: "8:30 AM to 4:30 PM",
    rate: "250",
    currency: "Rs.",
    active: true
  }];

  custList = [{
    name: "Old Cow",
    email: "oldcow@gmail.com",
    mob: "99999999999",
    whatsapp: "99999999999",
    others: "Long story",
    img: "assets/imgs/cow.jpg",
    address: {
      firstLine: "address 1",
      secondLine: "address 2",
      city: "city",
      pin: "pin",
      state: "state",
      country: "country",
    }
  },
  {
    name: "Old Cow",
    email: "oldcow@gmail.com",
    mob: "99999999999",
    whatsapp: "99999999999",
    others: "Long story",
    img: "assets/imgs/cow.jpg",
    address: {
      firstLine: "address 1",
      secondLine: "address 2",
      city: "city",
      pin: "pin",
      state: "state",
      country: "country",
    }
  }
  ];

  spList1 = [{
    name: "New Cow",
    email: "newcow@gmail.com",
    mob: "1111111111111",
    whatsapp: "11111111111",
    others: "Long story",
    img: "assets/imgs/cow.jpg",
    address: {
      firstLine: "address 1",
      secondLine: "address 2",
      city: "city",
      pin: "pin",
      state: "state",
      country: "country",
    }
  },
  {
    name: "New Cow",
    email: "newcow@gmail.com",
    mob: "11111111111",
    whatsapp: "1111111111",
    others: "Long story",
    img: "assets/imgs/cow.jpg",
    address: {
      firstLine: "address 1",
      secondLine: "address 2",
      city: "city",
      pin: "pin",
      state: "state",
      country: "country",
    }
  }
  ];

  services;
  public loading: Loading;
  user;

  constructor(
                public navCtrl: NavController
              , public navParams: NavParams
              , private mdlCtrl: ModalController
              , private firestore: FirestoreProvider
              , public loadingCtrl: LoadingController
              , public alertCtrl: AlertController
              , public config: OtuConfigProvider
              ) {
    this.firestore.getCurrentUser().subscribe((u) => {
      if(u != null && u.id != undefined) {
          this.user = u;
          this.firestore.getMyServices(u.id).valueChanges().subscribe((s) => {
          this.services = s;
        })
      }
    })
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SpHomePage');
  }

  selectTab(tab) {
    this.selectedTab = tab;
  }
  addSP() {
    let a = this.mdlCtrl.create('DynaPage');
    a.present();
    // a.onDidDismiss()
  }
  addCust() {

  }
  addService() {
    let a = this.mdlCtrl.create('DynaPage', {function: 'addService'});
    a.onDidDismiss((data) => {
      if(data == null) {
        return;
      }
      if(this.user != undefined && this.user.id != undefined && this.user.amId != undefined && data != undefined) {
        data['providerId'] = this.user.id;
        data['amId'] = this.user.amId;
        data['appId'] = this.config.getAppId();
        data['email'] = this.user.email,
        data['mob'] = this.user.mob,
        data['whatsapp'] = this.user.whatsapp
      }
      this.firestore.saveService(data).then((r) => {
        alert('Success');
        console.log(r);
        this.loading.dismiss();
      }, (error) => {
        this.loading.dismiss().then( () => {
          var errorMessage: string = error.message;
            let soundAlert = this.alertCtrl.create({
              message: errorMessage,
              buttons: [
                {
                  text: "Ok",
                  role: 'cancel'
                }
              ]
            });
            soundAlert.present();
        });
        this.navCtrl.setRoot(ErrorPage, {error: error});
      });

      this.loading = this.loadingCtrl.create({
        dismissOnPageChange: true,
      });
      this.loading.present();
    })
    a.present();
  }
  viewBookedService() {

  }

}



